.list {
  list-style: none;
  margin: 8px;
  padding: 0;

  & > li {
    width: 100%;
    .flex-row;
    justify-content: space-between;
    padding: 16px 56px;
    border-bottom: 2px solid @brighter;

    .right, .left {
      .flex-column;
      justify-content: space-between;
      height: 100%;
    }

    .right {
      align-items: flex-end;
    }

    .left {
      align-items: flex-start;
      max-width: 80%;
      overflow-wrap: anywhere;
    }
  }

  @media (max-width: 850px) {
    margin: 0;
    & > li {
      padding: 8px;
      flex-direction: column;

      .left {
        max-width: initial;
      }

    }
  }
}