button {
  color: #181717;
  background-color: #F3EFEE;
  border: 2px solid #5f5d5c;
  border-radius: 10px;
  padding: 12px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  transition-duration: 300ms;
  transition: visibility 0ms;
  cursor: pointer;
  min-width: 180px;
}
button:hover {
  background-color: #bebab9;
  color: #F3EFEE;
}
button:disabled {
  cursor: initial;
  background-color: #F3EFEE;
  color: #bebab9;
}
.button-big {
  display: flex;
  flex-direction: row;
