}
nav {
  position: absolute;
  width: 100%;
  z-index: 10;
  color: #EEE8E7;
}
nav #main-logo {
  max-width: 260px;
  width: 20%;
  margin: 20px 0 0 20px;
  transition: 10s ease-in-out 300ms;
  cursor: pointer;
}
nav #main-logo:hover {
  transform: rotate(360deg) scale(1.1);
}
nav #nav-links {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  position: fixed;
  top: 20px;
  right: 22px;
}
nav #nav-links a {
  margin: 0 10px;
  position: relative;
  top: 0;
  transition: 300ms;
  color: rgba(238, 232, 231, 0.85);
  text-decoration: underline rgba(238, 232, 231, 0.3);
  text-underline-offset: 12px;
  text-decoration-thickness: 2px;
}
nav #nav-links a:hover {
  top: 4px;
  color: #EEE8E7;
  text-decoration-color: #eee8e7;
  text-underline-offset: 15px;
  text-decoration-thickness: 3px;
}
nav #nav-links .user,
nav #nav-links .admin {
  transition: 300ms;
  text-decoration: none;
  border: 2px solid rgba(238, 232, 231, 0.3);
  padding: 8px;
}
nav #nav-links .user:hover,
nav #nav-links .admin:hover {
  border-color: #EEE8E7;
}
nav #nav-links svg {
  color: #EEE8E7;
  margin-left: 12px;
  width: 30px;
  height: 30px;
}
@media (max-width: 950px) {
  nav img,
  nav svg {
    display: none;
  }
  nav #nav-links {
    right: initial;
    left: initial;
    top: 12px;
    width: 100%;
    flex-wrap: wrap;
  }
  nav #nav-links a {
    margin-bottom: 20px;
  }
}
/* HOMEPAGE HAS CUSTOM STYLES FOR HEADER */
header:not(.homepage) {
  height: 350px;
}
header:not(.homepage) h1 {
  top: 150px;
  margin: 0;
  width: 100%;
  text-align: center;
  position: absolute;
}
header:not(.homepage) .background-anchor {
  height: 350px;
  background-color: black;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(3px) brightness(50%);
}
header:not(.homepage) .background-anchor::after {
  --gradient-height: 30%;
  display: block;
  position: absolute;
  height: var(--gradient-height, 20%);
  width: 100%;
  content: '';
  bottom: 0;
  height: 100%;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0) calc(100% - var(--gradient-height, 20%)), #000 100%), linear-gradient(to top, rgba(255, 255, 255, 0) calc(100% - var(--gradient-height, 20%)), #000 100%);
}
