.admin-users ul > li .left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.admin-users ul > li .right {
  align-items: flex-end;
}
.admin-users ul > li .left {
  align-items: flex-start;
  max-width: 80%;
  overflow-wrap: anywhere;
}
@media (max-width: 850px) {
  .admin-users ul {
    margin: 0;
  }
  .admin-users ul > li {
    padding: 8px;
    flex-direction: column;
  }
  .admin-users ul > li .left {
    max-width: initial;
  }
}
@media (max-width: 850px) {
  .admin-users ul .left,
  .admin-users ul .right {
    align-items: center !important;
  }
}
.admin-users button.delete {
  transition: 300ms;
  width: 210px;
  padding: 4px 0;
  margin-top: 6px;
  background-color: #181717;
  color: #ff3333;
  border-color: #ff3333;
}
.admin-users button.delete:hover {
  border-color: #181717;
  background-color: #ff3333;
  color: #181717;
  font-weight: bold;
}
.about-contact h1 {
  line-height: 50px;
}
.about-contact section {
  margin-top: 64px;
  text-align: left;
}
.about-contact .gallery {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about-contact .gallery > div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
.about-contact .gallery div img,
.about-contact .gallery div div {
  width: 50%;
}
.about-contact .gallery.bottom-margin {
  margin-bottom: 64px;
}
